@mixin atSmall 
  @media screen and (min-width: 320px) 
    @content

@mixin atMedium 
  @media screen and (min-width: 768px) 
    @content

@mixin atLarge 
  @media screen and (min-width: 1279px)
    @content

@mixin flex($fdirection, $jcontent, $acontent, $aitems)
  display: flex
  flex-direction: $fdirection
  justify-content: $jcontent
  align-content: $acontent
  align-items: $aitems
